
































































































import Vue from "vue";
type DataType = {
  message: string;
  checked: boolean;
  checkedNames: Array<string>;
  checkNamesCompute: string;
  picked: string;
  pick: string;
  a: string;
  toggle: boolean;
  selected: string;
  multiselect: Array<string>;
  options: Array<{ text: string; value: string }>;
  msg: string;
  age: number;
};

export default Vue.extend({
  name: "event1",
  data(): DataType {
    return {
      message: "",
      checked: false,
      checkedNames: [],
      checkNamesCompute: "",
      picked: "",
      pick: "",
      a: "a",
      toggle: false,
      selected: "",
      multiselect: [],
      options: [
        { text: "A", value: "1" },
        { text: "B", value: "2" },
        { text: "C", value: "3" },
      ],
      msg: "",
      age: 0,
    };
  },
  computed: {
    checkedNamesCompute: function (): string {
      return this.checkedNames.join(",");
    },
  },
});
